.repoList {
    position: relative;

    &_item {
        position: relative;
        padding-top: 30px;
        padding-bottom: 30px;
        list-style: none;
        border-bottom: 1px solid #A4A4A4;
        padding-left: 75px;

        &:hover {
            background-color: #efefef;
        }

        .avatar {
            display: inline-block;
            overflow: hidden;
            line-height: 1;
            vertical-align: middle;
            border-radius: 3px;
        }

        &_hanging_avatar {
            float: left;
            margin-left: -42px;
        }
    }

    &_stats {
        float: right;
        margin-top: 6px;
        font-size: 12px;
        font-weight: bold;
        color: #888;

        .repoList_stat_item {
            display: inline-block;
            margin-left: 8px;
            color: #888;
            text-decoration: none;
        }
    }

    &_name {
        margin: 0 0 8px;
        font-size: 20px;
        line-height: 1.2;

        .prefix, .slash {
            font-weight: normal;
        }

        .slash {
            margin-right: -4px;
            margin-left: -4px;
        }
    }

    &_description {
        max-width: 550px;
        margin-top: 8px;
        margin-bottom: 0;
        font-size: 14px;
        color: #666;
    }
}


