@import "common";

$header-ht: 50px;

.header {
    display: block; height: $header-ht;
    position: fixed;
    top: 0; left: 0; right: 0;
    padding:0 20px;
    background: rgba(244,244,244,0.95);
    @include clearfix;

    &_logo_invertocat {
        float: left;
        margin-right: 10px;
        margin-left: -2px;
        margin-top: 5px;
        color: #333;
        white-space: nowrap;

        .octicon_mark_github {
            float: left;
            font-size: 28px;
        }

        .octicon {
            display: inline-block;
            vertical-align: text-bottom;
            fill: currentColor;
        }

    }

    &_description {
        float: left;
        width: 360px;
        margin-right: 10px;

        .main-title {
            font-size: 28px;
            color: #55bbea;
            line-height: 36px;
        }

        .sub-title {
            font-size: 14px;
            line-height: 18px;
            color: #000;
            padding-top: 15px;
            display: inline-block;
            vertical-align: text-bottom;
        }
    }

    &_search_info {
        float: right;
        border-radius: 2px;
        border: 1px solid #4A4A4A;
        padding: 10px;
        font-size: 14px;
        line-height: 18px;
        background-color: #55bbea;;
        color: #fff;
        margin-top: 5px;

        .count {
            font-weight: bold;
        }

        .language {
            font-weight: bold;
            text-transform: capitalize;
        }
    }


}


