.repoHeader {
	height: 48px;
	border-bottom: 1px solid #A4A4A4;

	.autoComplete_container{
		float: left;
		margin-right: 15px;
		height: 100%;
		width: 250px;
    	padding-left: 5px;
    	padding-right: 5px;

    	.Select-control{
    		border-radius: 0px;
    		margin-top: 5px;
    	}
	}

	.stars_slider {
		float: left;
		position: relative;
		width: 250px;
		margin-right: 15px;

		.slider_markers {
			position: absolute;
			width: 240px;
			bottom: 35px;

			.start {
				left: 5px;
				position: absolute;
			}

			.end {
				right: 0px;
				position: absolute;
			}
		}

	}

	.rateLimit_info {
		width: 250px;
		float: left;
		height: 100%;
		float: left;

		.description {
			text-align: center;
			font-size: 14px;
			line-height: 18px; 

			&span {
				font-weight: bold;
			}
		}
	}

	.title {
		position: relative;
		padding: 5px;
		text-align: center;

		.helper_icon {
			position: absolute;
			left: 10px;
		}

		.text {
			font-size: 14px;
			line-height: 18px;
			font-weight: bold;
		}
	}



}


